@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
.upper{
  background: #FFC102;
  width: 337px;
  height: 270px;
  border-radius: 30px;
}
.upper1{
  width: 300px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 44px;
  padding-left: 13px;
  padding-top: 31px;
  align-items: center;
  font-family: Poppins;
}
.arrow{
  width: 22px;
  height: 20px;
  color: white;
}
.girl{
  width: 44px;
  height: 44px;
  border: 2px solid white;
  border-radius: 32px;
}

.upper2{
  display: flex;
  flex-direction: column;
  width: 293px;
  height: 143px;
  padding-top: 11px;
  padding-left: 20px;
  font-family: Poppins;
}
.popularicon{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 293px;
  height: 100px;
  gap: 10px;
}
.icon1{
  width: 91px;
  height: 100px;
  border-radius: 10px;
  background: white;
}
.icon1 p{
  text-align: left;
  margin-left: 11px;
  margin-top: 15px;
  font-family: Poppins;
   font-weight: 600;
   line-height: 23px;
  letter-spacing: 0em;
}
.icon1 img {
  width: 48px;
  height: 48px;
  padding: 2px 2px 4px 15px;
  margin-top: -10px;
}
