.down{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
}
.down1{
  width: 293px;
  height: 197px;
  margin-top: 6px;
  margin-left: 17px;
}
.downcontent{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-family: Poppins;
   font-weight: 600;
   font-size: 17px;
}
.downimg1{
  border: 1px solid #FFAD00;
}
.down2{
  margin-top: 33px;
  margin-left: 14px;
  margin-bottom: 30px;
}
.down3{
  margin-top: 15px;
  margin-left: 14px;
  margin-bottom: 30px;
}

@media screen and (max-width:300px) {
  .upper{
    margin-left: 10px;
  }
  .down{
    margin-left: 65px;
  }
}
@media screen and (max-width:350px) {
  .upper{
    margin-left: 10px;
  }
  .down{
    margin-left: 30px;
  }
}
@media screen and (max-width:370px) {
  .upper{
    margin-left: 8px;
  }
}
@media screen and (max-width:400px) {
  .upper{
    margin-left: 25px;
  }
}
@media screen and (max-width:415px) {
  .upper{
    margin-left: 30px;
  }
}